import { CBadge, CButton, CCard, CCardBody, CCardHeader, CCol, CFormInput, CFormLabel, CFormTextarea, CModal, CModalBody, CModalFooter, CModalHeader, CModalTitle, CRow } from "@coreui/react"
import { useEffect, useState } from "react"
import PageHeading from "src/components/PageHeading"
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import { addDoc, collection, getDocs, query, updateDoc, doc, orderBy, deleteDoc } from 'firebase/firestore'
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import { db } from 'src/firebase/FirebaseProvider'
import Swal from "sweetalert2";
import CIcon from "@coreui/icons-react";
import { cilCheck, cilTrash } from "@coreui/icons";
import moment from "moment";
import LoadingOverlay from "react-loading-overlay";
import Download from "../download/Download";

const BalanceSheet = () => {

    const [visible, setVisible] = useState(false)
    const [type, settype] = useState("")
    const [data, setData] = useState({
        amount: "",
        remark: "",
    })
    const [maintenanceData, setMaintenanceData] = useState([])
    const [deductionData, setDeductionData] = useState([])

    const [filteredMainData, setFilteredMain] = useState([])
    const [filteredDeduct, setFilteredDeduct] = useState([])

    const [searchValue, setSearchValue] = useState('')
    const [currentBalance, setCurrentBalance] = useState(0)
    const [isLoading, setIsLoading] = useState(false)

    const user = JSON.parse(localStorage.getItem("userDetails"))

    const onAdd = async () => {
        try {
            await addDoc(collection(db, "transactions"), {
                amount: data.amount,
                date: new Date().getTime(),
                remark: data.remark,
                status: "not-approved",
                transactionBy: user.firstName + " " + user.lastName,
                type: type === 'maintenance' ? 'add' : 'deduct'
            });
            Swal.fire({
                title: "Success",
                text: "Entry added successfully!",
                icon: "success",
                confirmButtonColor: "#006f95"
            }).then(e => {
                loadData()
                setVisible(false)
            })
        } catch (e) {
            console.log(e)
            Swal.fire({
                title: "Failed",
                text: "Failed to add transaction. Please try again",
                icon: "warning",
                confirmButtonColor: "#006f95"
            })
        }

    }

    const loadData = () => {
        setIsLoading(true)
        const q = query(collection(db, "transactions"), orderBy("date", "desc"));
        getDocs(q).then(rs => {
            const tempMain = []
            const tempDeduct = []
            let balance = 0
            rs.forEach(doc => {
                if (doc.data().type === 'add') {
                    if (doc.data().status === 'approved') {
                        balance += parseInt(doc.data().amount)
                    }
                    tempMain.push({ id: doc.id, dateAsString: moment(doc.data().date).format("DD MMM YYYY"), ...doc.data() })
                } else {
                    if (doc.data().status === 'approved') {
                        balance -= parseInt(doc.data().amount)
                    }
                    tempDeduct.push({ id: doc.id, dateAsString: moment(doc.data().date).format("DD MMM YYYY"), ...doc.data() })
                }
            })
            setCurrentBalance(balance)
            setMaintenanceData(tempMain)
            setDeductionData(tempDeduct)
            setIsLoading(false)
        });
    }

    const onApprove = async (transaction) => {
        const transactionRef = doc(db, "transactions", transaction.id);
        try {
            await updateDoc(transactionRef, {
                status: 'approved'
            });
            Swal.fire({
                title: "Success",
                text: "Transaction approved successfully!!",
                icon: "success",
                confirmButtonColor: "#006f95"
            }).then(e => {
                loadData()
            })
        } catch (e) {
            Swal.fire({
                title: "Failed",
                text: "Failed to approve transaction. Please try again",
                icon: "warning",
                confirmButtonColor: "#006f95"
            })
        }
    }

    const onDelete = async (transaction) => {
        const transactionRef = doc(db, "transactions", transaction.id);
        try {
            await deleteDoc(transactionRef);
            Swal.fire({
                title: "Success",
                text: "Transaction deleted successfully!!",
                icon: "success",
                confirmButtonColor: "#006f95"
            }).then(e => {
                loadData()
            })
        } catch (e) {
            Swal.fire({
                title: "Failed",
                text: "Failed to delete transaction. Please try again",
                icon: "warning",
                confirmButtonColor: "#006f95"
            })
        }
    }

    useEffect(() => {
        loadData()
    }, [])

    useEffect(() => {
        if (searchValue.length) {
            setFilteredMain(maintenanceData.filter(x =>
                x.remark.toLowerCase().includes(searchValue.toLowerCase()) ||
                x.dateAsString.toLowerCase().includes(searchValue.toLowerCase())
            ))

            setFilteredDeduct(deductionData.filter(x =>
                x.remark.toLowerCase().includes(searchValue.toLowerCase()) ||
                x.dateAsString.toLowerCase().includes(searchValue.toLowerCase())
            ))
        }
    }, [searchValue])

    return <>
        <LoadingOverlay
            active={isLoading}
            spinner
            text='Please wait...'
        >
            <CCard className="mb-4">
                <CCardHeader className='d-flex justify-content-between'>
                    <PageHeading>
                        Transactions
                    </PageHeading>
                    <h5>Balance : {currentBalance} &#8377;</h5>
                    {/* 
                <CButton variant="outline" onClick={() => setVisible(!visible)}>Add Deduction</CButton> */}
                </CCardHeader>
                <CCardBody>
                    <div className="d-flex justify-content-end">
                        {/* <CDropdown className="mb-3">
                        <CDropdownToggle placement="bottom-end" className="px-3 py-2" caret={true}>
                            <span>Select Filter</span>
                        </CDropdownToggle>
                        <CDropdownMenu className="pt-0" placement="bottom-end">
                            <CDropdownItem>
                                Download All Transactions
                            </CDropdownItem>
                            <CDropdownItem>
                                Last Month
                            </CDropdownItem>
                            <CDropdownItem>
                                This Year
                            </CDropdownItem>
                        </CDropdownMenu>
                    </CDropdown>{' '} */}
                        <CCol md={2} style={{ marginLeft: '10px' }} className="mb-3">
                            <CFormInput className="w-100" type="text"
                                placeholder="Search by month"
                                defaultValue={searchValue}
                                onChange={e => setSearchValue(e.target.value)}
                            ></CFormInput>
                        </CCol>
                    </div>
                    <CCard className="mb-4">
                        <CCardHeader className='d-flex justify-content-between'>
                            <h6 style={{ marginTop: '10px' }}>
                                Maintenance
                            </h6>
                            <span>
                                <CButton variant="outline" onClick={() => { settype("maintenance"); setVisible(!visible) }}>Add Entry</CButton>
                            </span>
                        </CCardHeader>
                        <CCardBody style={{ maxHeight: '520px', overflowY: 'auto' }}>
                            <Table>
                                <Thead>
                                    <Tr>
                                        <Th>Entry By</Th>
                                        <Th>Amount</Th>
                                        <Th>Date</Th>
                                        <Th>Remark</Th>
                                        <Th>Status</Th>
                                        {user.isAdmin && <Th>Action</Th>}
                                    </Tr>
                                </Thead>
                                <Tbody>
                                    {(searchValue.length ? filteredMainData : maintenanceData).map(x => <Tr>
                                        <Td>{x.transactionBy}</Td>
                                        <Td>{x.amount} &#8377;</Td>
                                        <Td>{x.dateAsString}</Td>
                                        <Td>{x.remark}</Td>
                                        <Td>{x.status === 'approved' ? <CBadge color="success">Approved</CBadge> : <CBadge color="warning">Waiting</CBadge>}</Td>
                                        {user.isAdmin && <Td>
                                            <CButton onClick={() => onApprove(x)} disabled={x.status === 'approved'}>
                                                <CIcon icon={cilCheck} />
                                                Approve
                                            </CButton>
                                        </Td>}
                                    </Tr>)}
                                </Tbody>
                            </Table>
                        </CCardBody>
                    </CCard>

                    <CCard className="mb-4">
                        <CCardHeader className='d-flex justify-content-between'>
                            <h6 style={{ marginTop: '10px' }}>
                                Deductions
                            </h6>
                            <CButton variant="outline" onClick={() => { settype("deduction"); setVisible(!visible) }}>Add Entry</CButton>
                        </CCardHeader>
                        <CCardBody style={{ maxHeight: '520px', overflowY: 'auto' }}>
                            <Table>
                                <Thead>
                                    <Tr>
                                        <Th>Entry By</Th>
                                        <Th>Amount</Th>
                                        <Th>Date</Th>
                                        <Th>Remark</Th>
                                        <Th>Status</Th>
                                        {user.isAdmin && <Th>Action</Th>}
                                    </Tr>
                                </Thead>
                                <Tbody>
                                    {(searchValue.length ? filteredDeduct : deductionData).map(x => <Tr>
                                        <Td>{x.transactionBy}</Td>
                                        <Td>{x.amount} &#8377;</Td>
                                        <Td>{x.dateAsString}</Td>
                                        <Td>{x.remark}</Td>
                                        <Td>{x.status === 'approved' ? <CBadge color="success">Approved</CBadge> : <CBadge color="warning">Waiting</CBadge>}</Td>
                                        <Td>
                                            {user.isAdmin && <><CButton onClick={() => onApprove(x)} disabled={x.status === 'approved'}>
                                                <CIcon icon={cilCheck} />
                                                Approve
                                            </CButton>
                                            {x.status !== 'approved' && <CButton color="danger" onClick={() => onDelete(x)} disabled={x.status === 'approved'}>
                                                    <CIcon icon={cilTrash} />
                                                    Delete
                                                </CButton>}
                                            </>}
                                        </Td>
                                    </Tr>)}
                                </Tbody>
                            </Table>
                        </CCardBody>
                    </CCard>
                </CCardBody>
            </CCard>
            <CModal visible={visible} onClose={() => setVisible(false)}>
                <CModalHeader>
                    <CModalTitle>{type === 'deduction' ? 'Entry for deduction' : 'Entry for maintenance'}</CModalTitle>
                </CModalHeader>
                <CModalBody>
                    <CRow>
                        <CCol md={12} className="p-1">
                            <CFormLabel htmlFor="inputFirstName">Amount</CFormLabel>
                            <CFormInput type="number" defaultValue={data.amount} onChange={e => setData({ ...data, amount: e.target.value })} />
                        </CCol>
                        <CCol md={12} className="p-1">
                            <CFormLabel htmlFor="inputLastName">Remark</CFormLabel>
                            <CFormTextarea defaultValue={data.remark} onChange={e => setData({ ...data, remark: e.target.value })}></CFormTextarea>
                        </CCol>
                    </CRow>
                </CModalBody>
                <CModalFooter>
                    <CButton color="secondary" onClick={() => setVisible(false)} onChange={e => setData({ ...data, remark: e.target.value })}>
                        Close
                    </CButton>
                    <CButton color="primary" onClick={onAdd}>Add</CButton>
                </CModalFooter>
            </CModal>
        </LoadingOverlay>
    </>
}

export default BalanceSheet