
import { CCard, CCardHeader, CCardBody, CCol, CButton } from '@coreui/react'
import { collection, getDocs, query, orderBy } from 'firebase/firestore'
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import { useEffect, useState } from 'react';
import LoadingOverlay from 'react-loading-overlay';
import { db } from 'src/firebase/FirebaseProvider'
import moment from 'moment';
import PageHeading from 'src/components/PageHeading';
import { useNavigate } from 'react-router-dom';
import jsPDF from 'jspdf';
import fileDownload from 'js-file-download';
import 'jspdf-autotable';

const Download = () => {

    const navigate = useNavigate()

    const [Transactions, setTransactions] = useState([])

    const [currentBalance, setCurrentBalance] = useState(0)
    const [isLoading, setIsLoading] = useState(false)

    const loadData = () => {

        setIsLoading(true)
        const q = query(collection(db, "transactions"), orderBy("date", "desc"));

        getDocs(q).then(rs => {
            const tempMain = []
            let balance = 0
            rs.forEach(doc => {
                if (doc.data().type === 'add') {
                    if (doc.data().status === 'approved') {
                        balance += parseInt(doc.data().amount)
                    }

                } else {
                    if (doc.data().status === 'approved') {
                        balance -= parseInt(doc.data().amount)
                    }
                }
                tempMain.push({ Date: moment(doc.data().date).format("DD MMM YYYY"), ...doc.data() })
            })
            setCurrentBalance(balance)
            setTransactions(tempMain)
            setIsLoading(false)
        });
    }

    const onClickDownload = () => {
        const data = []
        Transactions.forEach(x => {
            data.push({
                "Entry By" : x.transactionBy,
                "Amount" : x.amount,
                "Type" : x.type === "add" ? "Credit" : "Deduction",
                "Date" : x.Date,
                "Status" : x.status.charAt(0).toUpperCase() + x.status.slice(1),
                "Remark" : x.remark,
            })
        })

        const pdf = new jsPDF();
        const tableHeaders = Object.keys(data[0]);
        const tableData = data.map(obj => Object.values(obj));

        pdf.text('Renukai Residency - Transactions', 70, 10);
        pdf.autoTable({
            startY: 15,
            head: [tableHeaders],
            body: tableData,
            headStyles: {
                fillColor: "#1a7da0", // Set a custom fill color for the table headers
                textColor: [255, 255, 255], // Set a custom text color for the table headers
                fontSize: 12, // Set a custom font size for the table headers
                fontStyle: 'bold', // Set the font style to bold for the table headers
            },
        });

          const pdfData = pdf.output();
          fileDownload(pdfData, 'Renukai Residency Transactions.pdf');
    }

    useEffect(() => {
        loadData()
    }, [])

    return <>
        <LoadingOverlay active={isLoading}
            spinner
            text='Please wait...'>
            <CCol>
                <CCard>
                    <CCardHeader className='d-flex justify-content-between'>
                        <PageHeading>
                            Download Transactions
                        </PageHeading>
                        {/* <CButton variant="outline" onClick={onDateJump}>Date Jump</CButton> */}
                    </CCardHeader>
                    <CCardBody>
                        <CButton variant='outline' className='w-100 mb-1' onClick={onClickDownload}>Download All Transactions</CButton>
                        <CButton variant='outline' className='w-100 mb-1'>Last Month</CButton>
                        <CButton variant='outline' className='w-100 mb-1'>This Year</CButton>
                        <CButton variant='outline' className='w-100 mb-1'>Last Year</CButton>
                    </CCardBody>
                </CCard>
            </CCol>
            {/* <CCol className='mt-3'>
                <CCard>
                    <CCardHeader className='d-flex justify-content-between'>
                        <PageHeading>
                            Download Water Days
                        </PageHeading>
                    </CCardHeader>
                    <CCardBody>
                        <CButton variant='outline' className='w-100 mb-1'>Download Water Days</CButton>
                    </CCardBody>
                </CCard>
            </CCol > */}
        </LoadingOverlay>
    </>
}

export default Download